@import 'grid';

$accent-1-color: #d01380;
$accent-2-color: #fde9f0;
$primary-1-color: #00bcd4;

html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  -webkit-tap-highlight-color: rgba(0,0,0,0) !important;
}

button:focus {
  outline:0;
}

a {
  cursor: pointer;
}

#app {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

.header {
  background-color: $accent-1-color;
  height: 10%;
  color: white;

  a {
    text-decoration: none;
    color: inherit;
  }
}

.content {
  height: 90%;
  background-color: #eeeeee;
}

.bg-primary-1-color {
  background-color: $primary-1-color;
}

.bg-accent-2-color {
  background-color: $accent-2-color;
}

.txt-bold {
  font-weight: bold;
}

.txt-normal {
  font-weight: normal;
}

.txt-l {
  font-size: 1.25rem !important;
}

.txt-xl {
  font-size: 1.5rem !important;
}

.no-margin {
  margin: 0 !important;
}

.margin-bottom {
  margin-bottom: 8px;
}

.margin-top {
  margin-top: 8px;
}

@media (max-width: 599px) {
  //SMALL SCREEN
  .margin-bottom-sm {
    margin-bottom: 8px;
  }

  .margin-top-sm {
    margin-top: 8px;
  }
}

@media (min-width: 600px) {
  // BIGGER THAN SMALL SCREEN
  .margin-right-gt-sm {
    margin-right: 8px;
  }
}

.basic-detail-container {
  background-color: #eeeeee;
  padding-bottom:8px;
  padding-top: 8px;
  margin-top: -8px;
  margin-left:-8px;
  margin-right:-8px;

  .basic-detail {
    margin-left:8px;
    margin-right:8px;
  }
}

.events-container {
  background-color: #cccccc;
  padding-bottom:8px;
  margin-left:-8px;
  margin-right:-8px;

  .events-container-header {
    padding-top:16px;
    padding-bottom:16px;
    margin-left:8px;
    margin-right:8px;
  }

  .events {
    margin-left:8px;
    margin-right:8px;
  }
}

.accounting-container {
  background-color: #eeeeee;
  padding-bottom:8px;
  margin-top: -16px;
  margin-left:-8px;
  margin-right:-8px;
  transform: translateY(8px);

  .accounting {
    margin-left:8px;
    margin-right:8px;
    padding-top: 8px;
  }
}

.edit-container-mobile {
  input {
    font-size: 0.9rem !important;
  }
}

.container-enter {
  opacity: 0.01;
}

.container-enter.container-enter-active {
  opacity: 1;
  transition: opacity 500ms ease-in;
}

.container-leave {
  opacity: 1;
}

.container-leave.container-leave-active {
  opacity: 0.01;
  transition: opacity 300ms ease-in;
}

@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(180deg);
  }
}
.rotating-animation {
  animation: rotating 1s linear infinite;
}


.appointment-card {
    box-shadow: rgba(0, 0, 0, 0.117647) 0px 1px 6px, rgba(0, 0, 0, 0.117647) 0px 1px 4px;
    border-radius: 2px;

    .date-container {

    }

    .detail-container {

    }
}



